.schema-preview {
  width: 350px;
  height: 511px;
  border-radius: 0 0 5px 5px;
  overflow-y: auto;
}
.schema-preview h3 {
  padding-top: 14px;
  color: #2A9BFF;
  font-size: 16px;
  margin-left: 15px;
}
.schema-preview .schema-preview-divider {
  width: 320px;
  margin-left: 15px ;
  border: 0.8px solid #DADADA;
  opacity: 0.6;
}
.schema-preview .schema-preview-menu {
  width: 350px;
}
.schema-preview .schema-preview-menu .ant-menu-submenu.ant-menu-submenu-inline {
  color: #94AAE1;
  font-size: 16px;
  font-weight: bold;
}
.schema-preview .schema-preview-menu .ant-menu-submenu.ant-menu-submenu-inline .ant-menu-item.ant-menu-item-only-child {
  color: #94AAE1;
  font-size: 14px;
  font-weight: normal;
  margin-left: 15px;
}
.schema-preview .schema-preview-menu .ant-menu-submenu.ant-menu-submenu-inline .ant-menu-item.ant-menu-item-only-child .ant-menu-item-active {
  color: #2A9BFF;
}
.schema-preview .schema-preview-menu .ant-menu-submenu.ant-menu-submenu-inline .ant-menu-item.ant-menu-item-only-child .ant-menu-item-selected {
  color: #2A9BFF;
}
.schema-preview .schema-preview-card {
  border-radius: 15px;
  height: 245px;
}
.schema-preview .schema-preview-card .schema-preview-card-div {
  overflow-y: auto;
}
.schema-preview .schema-preview-card .schema-preview-card-div .schema-preview-card-question {
  height: 57px;
}
.schema-icon-pic {
  width: 24px;
  height: 24px;
  margin-right: 2px;
}
.ant-modal.schema-preview-card .ant-modal-content {
  width: 640px ;
  height: 285px;
  border-radius: 15px;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-header {
  width: 100%;
  padding: 0;
  height: 40px;
  background-color: #2A9BFF;
  border-radius: 15px 15px 0 0;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-header .ant-modal-title {
  height: 40px;
  width: 300px;
  font-size: 16px;
  color: white;
  padding-top: 10px;
  padding-left: 23px;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-body {
  padding: 10px 0 0 0;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-body .schema-preview-card-div {
  display: inline-block;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-body .schema-preview-card-div .schema-preview-card-question {
  display: -webkit-flex;
  display: flex;
  height: 57px;
  padding-top: 17px ;
  padding-left: 40px;
  color: #383838;
  font-size: 16px;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-body .schema-preview-card-div .schema-preview-card-question .card-question-content {
  width: 482px;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-body .schema-preview-card-div .schema-preview-card-question .card-question-btn {
  float: right;
  padding: 0;
  width: 100px;
  height: 25px;
  vertical-align: center;
  border-radius: 4px;
  background-image: url(/static/media/generate.a2b055da.svg);
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-body .schema-preview-card-div .schema-preview-divider {
  width: 604px;
  margin-left: 18px ;
  border: 0.8px solid #DADADA;
  opacity: 0.6;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-close {
  vertical-align: middle ;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-close .ant-modal-close-x {
  padding-top: 8px;
  width: 40px;
  height: 40px;
  text-align: center;
}
.ant-modal.schema-preview-card .ant-modal-content .ant-modal-close .ant-modal-close-x .schema-icon-close {
  width: 23px;
  display: block;
  height: 23px;
}
.schema-icon-empty {
  width: 16px;
  height: 16px;
}
.info-empty .ant-modal-content {
  border-radius: 15px;
}
.info-empty .ant-modal-content .ant-btn.ant-btn-primary {
  background-color: #2A9BFF;
}
